import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { processDescription } from '../game-logic/processDescription';
import { has } from '../jsUtils';

let orBar = (s) => (s == null ? "-" : s);
let orOne = (s) => (s == null ? 1 : s);
let getGold =(g,m,a,h,d) => {
  if (g) return g;
  if (m || h) return " ";
  if (a) return "Pay " + a;
  if (d.indexOf("(Discard 1 to play) ") > -1) {
      return "Discard 1";
  }
  return "Free";
};

let range = (n) => {
    let ret:number[] = [];
    for (var i = 0; i < n; i++) {
        ret.push(i);
    }
    return ret;
}

export var GoldBox = (props : {gold: number, className: string}) => {
     return <div className={props.className}>
         <img className="gold" src="images/coin.png" />
         <div className="manacost"> {props.gold} </div>
     </div>;
}

export var ManaBox = (props : {mana: number, className: string}) => {
     return <div className={props.className}>
         <img className="mana" src="images/blast.png" />
         <div className="manacost"> {props.mana} </div>
     </div>;
}

export var TextBox = (props: {text: string, className: string}) => {
     return <div className={props.className}>
         <div className="manacost"> {props.text} </div>
     </div>;
}

export const CostBox = (props: {gold: number, mana: number}) => (
    <div className="costbox">
        <GoldBox className="resourcecost" gold={props.gold}/>
        <ManaBox className="resourcecost" mana={props.mana}/>
    </div>);

export var ImageFill = (props: {image: string, borderColor: string, borderRadius: string}) => {
    let style = {
        backgroundImage: `url("${props.image}")`,
        backgroundRepeat: 'no-repeat',
        backgroundPosition: '50% 50%',
        backgroundSize: 'cover',
        borderColor: props.borderColor,
        borderRadius: props.borderRadius
    };
    return <div className="avatar" style={style}/>;
}

export var CardSpell = (props) => (<div className="cardspell">
    <div className="cardspell-back" style={props.TYPE.indexOf("Equip") >= 0 ? {background: "rgb(204, 204, 220) url(\"images/spell.png\")"} : {}}/>
    <div className="descriptiontext" dangerouslySetInnerHTML={{__html: props.DESCRIPTION}}
         style={{borderRadius: '1mm', borderColor: "rgba(0,0,0,1.0)",
            backgroundImage: props.TYPE.indexOf("Equip") > 0 ? 'url("images/sword.png")' : 'url("images/spell-book-cut.png")',
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center',
            backgroundSize: 'contain'
         }}/>
    <div className="flavourtext" dangerouslySetInnerHTML={{__html: props.FLAVOURTEXT}}/>
    <div className="typebox" dangerouslySetInnerHTML={{__html: props.TYPE}}/>
    <div className="deckflagsbox">
        {props.DECK_FLAGS.map(d => (
            <img className="statimage" src={d} />
        ))}
    </div>
    <ImageFill image={props.IMAGE_FILE} borderColor="rgba(0,0,0,1.0)" borderRadius="1mm"/>
    <div className="namefield" style={{borderRadius: '1mm', borderColor: "rgba(0,0,0,1.0)"}}>
      <div className="name" dangerouslySetInnerHTML={{__html: props.NAME}}/>
    </div>
    {CostBox(props)}
  </div>);

export interface CardSpriteProps {
    name: string;
    descriptionText: string;
    flavourText: string;
    typeText: string;
    cardTypes: string[];
    width, height: number;
}

function _cardBg({cardTypes}: CardSpriteProps) {
    if (has(cardTypes, "spell")) {
        return 'rgb(204, 204, 220) url("images/spell.png")';
    } else if (has(cardTypes, "equip")) {
        return 'rgb(204, 204, 220) url("images/equip.png")';
    } else {
        return "rgb(204, 204, 220) url(\"images/spell.png\")";
    }
}

// props.TYPE.indexOf("Equip") > 0 ? 'url("images/sword.png")' : 'url("images/spell-book-cut.png")',
const _cardDescriptionBoxBg = ({cardTypes}: CardSpriteProps) => {
    if (has(cardTypes, "spell")) {
        return "url(\"images/spell-book-cut.png\")";
    } else if (has(cardTypes, "equip")) {
        return "url(\"images/sword.png\")";
    } else {
        return "url(\"images/pawn.png\")";
    }
};

// width: 63mm;
// height: 88mm;

export const CardSprite = (props: CardSpriteProps) => (
    <div style={{
        userSelect: "none",
        backfaceVisibility: "hidden",
        border: "0.3mm solid black",
        borderRadius: "0.5mm",
        width: `${props.width}px`,
        height: `${props.height}px`,
        zIndex: 0
    }}>
        <div style={{
            position: "absolute",
            userSelect: "none",
            backfaceVisibility: "hidden",
            background: _cardBg(props),
            border: "0.3mm solid black",
            borderRadius: "0.5mm",
            width: `${props.width}px`,
            height: `${props.height}px`,
            zIndex: -1
        }}/>
        <div className="descriptiontext" dangerouslySetInnerHTML={{__html: props.descriptionText}}
            style={{
                borderRadius: '1mm', borderColor: "rgba(0,0,0,1.0)",
                backgroundImage: _cardDescriptionBoxBg(props),
                backgroundRepeat: 'no-repeat',
                backgroundPosition: 'center',
                backgroundSize: 'contain'
            }}/>
        <div className="flavourtext" dangerouslySetInnerHTML={{__html: props.flavourText}}
            style={{
                marginTop: "86mm",
                marginLeft: "2mm",
                width: "50mm"}}/>
        <div className="typebox" dangerouslySetInnerHTML={{__html: props.typeText}}/>
    </div>
);

export var CardUnit = (props) => (<div className="cardunit">
    <div className="cardunit-back"/>
    <div className="descriptiontext" dangerouslySetInnerHTML={{__html: props.DESCRIPTION}}
         style={{borderColor: "rgba(0,20,0,1.0)",
            backgroundImage: `url("images/${props.TYPE.indexOf("Unique ") === -1 ? 'pawn' : 'star-unique'}.png")`,
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center',
            backgroundSize: 'contain'
         }}/>
    <div className="flavourtext" dangerouslySetInnerHTML={{__html: props.FLAVOURTEXT}}/>
    <div className="typebox" dangerouslySetInnerHTML={{__html: props.TYPE.replace("Unique ", "")}}/>
    <ImageFill image={props.IMAGE_FILE} borderColor="rgba(0,20,0,1.0)" borderRadius="1mm"/>
    <div className="statbox">
      <div className="statline">
         {range(props.HEALTH).map(i => <img key={i} className="statimage" src="images/health.png" />)}
      </div>
      <div className="statline">
        {range(props.RANGE).map(i => <img key={i} className="statimage" src="images/range.png" />)}
      </div>
    </div>
    <div className="namefield" style={{borderRadius: '1mm', borderColor: "rgba(0,0,0,1.0)"}}>
      <div className="name" dangerouslySetInnerHTML={{__html: props.NAME}}/>
    </div>
    {CostBox(props)}
  </div>);

export class Card extends React.Component {
    props: any;
    state: {hovering: boolean};

    constructor(props) {
      super(props);
      this.state = {hovering: false};
    }

    render() {
        let {card, mini, outline, transform, className, position, style, width, height} = this.props;
        card.Description = card.Description || '';
        card.DeckFlags = card.DeckFlags || [];
        card.FlavourText = card.FlavourText || '';
        card.Traits = card.Traits || [];
        if (typeof card.Range === "undefined") {
            var CardType = CardSpell;
        } else {
            var CardType = CardUnit;
        }
        let traits = (card.Traits || []).filter(s => s !== 'Structure');
        let {amount} = this.props;
        if ((amount+'').indexOf('0') >= 0) {
            amount = '';
        }
        let cardObject = <CardType
            NAME={card.name}
            gold={card.Gold}
            COLOURLESS={card.AnyCost || ' '}
            mana={card.Miracle}
            DISCARD={card.DiscardCost || " "}
            HEALTH_COST={card.HealthCost || " "}
            USE_MANA={!!card.Miracle}
            USE_GOLD={!!card.Gold}
            USE_DISCARD={!!card.DiscardCost}
            USE_HEALTH={!!card.HealthCost}
            DECK_FLAGS={card.DeckFlags}
            TYPE={(amount || "")+ " " + traits.join(" ")}
            DESCRIPTION={processDescription(card.Description)}
            FLAVOURTEXT={processDescription(card.FlavourText)}
            HEALTH={orBar(card.Health)}
            IMAGE_FILE={card.Image || "http://vignette4.wikia.nocookie.net/assassinscreed/images/a/af/Question_mark.png/revision/latest/scale-to-width-down/380?cb=20121118055707"}
            RANGE={orOne(card.Range)}/>;
        let zIndex = (this.state.hovering || outline) ? 9999 : 0;
        let onMouseEnter = () => {
            this.setState({hovering: true});
        };
        let onMouseLeave = () => {
            this.setState({hovering: false});
        };
        position = position || 'relative';

        // TODO add more fields as needed
        let {marginRight, opacity} = style || {marginRight: undefined, opacity: 1};
        return <div onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} ref="div" className={className}
            style={{
                marginRight,
                position,
                outline,
                width: `${width}px`,
                height: `${height}px`,
                display: "inline-block",
                overflow: "visible",
                backfaceVisibility: 'hidden',
                zIndex,
                opacity,
                transform: `scale(0.751) translate(${-width/4 + 14}px, ${-height/4+18}px)`
            }}
            onContextMenu={this.props.onRightClick} onClick={this.props.onClick}> {cardObject} </div>;
    }
    // translate(${-width/2}px, ${-height/2}px)
};



